.button {
    margin-top: 1em;
}

button {
    color: white;
    background-color: #3b5bb2;
    border: 2px white solid;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05), 0 3px 10px 0 rgba(0, 0, 0, 0.05);
    padding: 1em 2em;
    cursor: pointer;
}

button:hover {
    background-color: #34509c;
}